import { _pluck, _range } from "../core/core.internal";
import $ from "jquery";

const _dt_ext_footer = {
  _: function (settings, cell, classes) {
    cell.addClass(classes.tfoot.cell);
  },
};

const _dt_ext_header = {
  _: function (settings, cell, classes) {
    cell.addClass(classes.thead.cell);

    if (!settings.oFeatures.bSort) {
      cell.addClass(classes.order.none);
    }

    var titleRow = settings.titleRow;
    var headerRows = cell.closest("thead").find("tr");
    var rowIdx = cell.parent().index();

    // Conditions to not apply the ordering icons
    if (
      // Cells and rows which have the attribute to disable the icons
      cell.attr("data-dt-order") === "disable" ||
      cell.parent().attr("data-dt-order") === "disable" ||
      // titleRow support, for defining a specific row in the header
      (titleRow === true && rowIdx !== 0) ||
      (titleRow === false && rowIdx !== headerRows.length - 1) ||
      (typeof titleRow === "number" && rowIdx !== titleRow)
    ) {
      return;
    }

    // No additional mark-up required
    // Attach a sort listener to update on sort - note that using the
    // `DT` namespace will allow the event to be removed automatically
    // on destroy, while the `dt` namespaced event is the one we are
    // listening for
    $(settings.nTable).on(
      "order.dt.DT column-visibility.dt.DT",
      function (e, ctx, column) {
        if (settings !== ctx) {
          // need to check this this is the host
          return; // table, not a nested one
        }

        var sorting = ctx.sortDetails;

        if (!sorting) {
          return;
        }

        var orderedColumns = _pluck(sorting, "col");

        // This handler is only needed on column visibility if the column is part of the
        // ordering. If it isn't, then we can bail out to save performance. It could be a
        // separate event handler, but this is a balance between code reuse / size and performance
        // console.log(e, e.name, column, orderedColumns, orderedColumns.includes(column))
        if (
          e.type === "column-visibility" &&
          !orderedColumns.includes(column)
        ) {
          return;
        }

        var i;
        var orderClasses = classes.order;
        var columns = ctx.api.columns(cell);
        var col = settings.aoColumns[columns.flatten()[0]];
        var orderable = columns.orderable().includes(true);
        var ariaType = "";
        var indexes = columns.indexes();
        var sortDirs = columns.orderable(true).flatten();
        var tabIndex = settings.iTabIndex;
        var canOrder = ctx.orderHandler && orderable;

        cell
          .removeClass(orderClasses.isAsc + " " + orderClasses.isDesc)
          .toggleClass(orderClasses.none, !orderable)
          .toggleClass(
            orderClasses.canAsc,
            canOrder && sortDirs.includes("asc")
          )
          .toggleClass(
            orderClasses.canDesc,
            canOrder && sortDirs.includes("desc")
          );

        // Determine if all of the columns that this cell covers are included in the
        // current ordering
        var isOrdering = true;

        for (i = 0; i < indexes.length; i++) {
          if (!orderedColumns.includes(indexes[i])) {
            isOrdering = false;
          }
        }

        if (isOrdering) {
          // Get the ordering direction for the columns under this cell
          // Note that it is possible for a cell to be asc and desc sorting
          // (column spanning cells)
          var orderDirs = columns.order();

          cell.addClass(
            orderDirs.includes("asc")
              ? orderClasses.isAsc
              : "" + orderDirs.includes("desc")
              ? orderClasses.isDesc
              : ""
          );
        }

        // Find the first visible column that has ordering applied to it - it get's
        // the aria information, as the ARIA spec says that only one column should
        // be marked with aria-sort
        var firstVis = -1; // column index

        for (i = 0; i < orderedColumns.length; i++) {
          if (settings.aoColumns[orderedColumns[i]].bVisible) {
            firstVis = orderedColumns[i];
            break;
          }
        }

        if (indexes[0] == firstVis) {
          var firstSort = sorting[0];
          var sortOrder = col.asSorting;

          cell.attr(
            "aria-sort",
            firstSort.dir === "asc" ? "ascending" : "descending"
          );

          // Determine if the next click will remove sorting or change the sort
          ariaType = !sortOrder[firstSort.index + 1] ? "Remove" : "Reverse";
        } else {
          cell.removeAttr("aria-sort");
        }

        // Make the headers tab-able for keyboard navigation
        if (orderable) {
          var orderSpan = cell.find(".dt-column-order");

          orderSpan
            .attr("role", "button")
            .attr(
              "aria-label",
              orderable
                ? col.ariaTitle + ctx.api.i18n("oAria.orderable" + ariaType)
                : col.ariaTitle
            );

          if (tabIndex !== -1) {
            orderSpan.attr("tabindex", tabIndex);
          }
        }
      }
    );
  },
};

const _dt_ext_layout = {
  _: function (settings, container, items) {
    var classes = settings.oClasses.layout;
    var row = $("<div/>")
      .attr("id", items.id || null)
      .addClass(items.className || classes.row)
      .appendTo(container);

    _dt_ext_layout._forLayoutRow(items, function (key, val) {
      if (key === "id" || key === "className") {
        return;
      }

      var klass = "";

      if (val.table) {
        row.addClass(classes.tableRow);
        klass += classes.tableCell + " ";
      }

      if (key === "start") {
        klass += classes.start;
      } else if (key === "end") {
        klass += classes.end;
      } else {
        klass += classes.full;
      }

      $("<div/>")
        .attr({
          id: val.id || null,
          class: val.className ? val.className : classes.cell + " " + klass,
        })
        .append(val.contents)
        .appendTo(row);
    });
  },

  // Shared for use by the styling frameworks
  _forLayoutRow: function (items, fn) {
    // As we are inserting dom elements, we need start / end in a
    // specific order, this function is used for sorting the layout
    // keys.
    var layoutEnum = function (x) {
      switch (x) {
        case "":
          return 0;
        case "start":
          return 1;
        case "end":
          return 2;
        default:
          return 3;
      }
    };

    Object.keys(items)
      .sort(function (a, b) {
        return layoutEnum(a) - layoutEnum(b);
      })
      .forEach(function (key) {
        fn(key, items[key]);
      });
  },
};

const _dt_ext_paging_button = {
  _: function (settings, buttonType, content, active, disabled) {
    var classes = settings.oClasses.paging;
    var btnClasses = [classes.button];
    var btn;

    if (active) {
      btnClasses.push(classes.active);
    }

    if (disabled) {
      btnClasses.push(classes.disabled);
    }

    if (buttonType === "ellipsis") {
      btn = $('<span class="ellipsis"></span>').html(content)[0];
    } else {
      btn = $("<button>", {
        class: btnClasses.join(" "),
        role: "link",
        type: "button",
      }).html(content);
    }

    return {
      display: btn,
      clicker: btn,
    };
  },
};

const _dt_ext_paging_container = {
  _: function (settings, buttons) {
    // No wrapping element - just append directly to the host
    return buttons;
  },
};

export const _dt_ext_renderer = {
  pageButton: {},
  header: _dt_ext_header,
  pagingButton: _dt_ext_paging_button,
  pagingContainer: _dt_ext_paging_container,
  footer: _dt_ext_footer,
  layout: _dt_ext_layout,
};

export const _dt_ext_pagination = {
  simple: function () {
    return ["previous", "next"];
  },

  full: function () {
    return ["first", "previous", "next", "last"];
  },

  numbers: function () {
    return ["numbers"];
  },

  simple_numbers: function () {
    return ["previous", "numbers", "next"];
  },

  full_numbers: function () {
    return ["first", "previous", "numbers", "next", "last"];
  },

  first_last: function () {
    return ["first", "last"];
  },

  first_last_numbers: function () {
    return ["first", "numbers", "last"];
  },

  // For testing and plug-ins to use
  _numbers: _pagingNumbers,

  // Number of number buttons - legacy, use `numbers` option for paging feature
  numbers_length: 7,
};

/**
 * Compute what number buttons to show in the paging control
 *
 * @param {*} page Current page
 * @param {*} pages Total number of pages
 * @param {*} buttons Target number of number buttons
 * @param {boolean} addFirstLast Indicate if page 1 and end should be included
 * @returns Buttons to show
 */
export function _pagingNumbers(page, pages, buttons, addFirstLast) {
  var numbers = [],
    half = Math.floor(buttons / 2),
    before = addFirstLast ? 2 : 1,
    after = addFirstLast ? 1 : 0;

  if (pages <= buttons) {
    numbers = _range(0, pages);
  } else if (buttons === 1) {
    // Single button - current page only
    numbers = [page];
  } else if (buttons === 3) {
    // Special logic for just three buttons
    if (page <= 1) {
      numbers = [0, 1, "ellipsis"];
    } else if (page >= pages - 2) {
      numbers = _range(pages - 2, pages);
      numbers.unshift("ellipsis");
    } else {
      numbers = ["ellipsis", page, "ellipsis"];
    }
  } else if (page <= half) {
    numbers = _range(0, buttons - before);
    numbers.push("ellipsis");

    if (addFirstLast) {
      numbers.push(pages - 1);
    }
  } else if (page >= pages - 1 - half) {
    numbers = _range(pages - (buttons - before), pages);
    numbers.unshift("ellipsis");

    if (addFirstLast) {
      numbers.unshift(0);
    }
  } else {
    numbers = _range(page - half + before, page + half - after);
    numbers.push("ellipsis");
    numbers.unshift("ellipsis");

    if (addFirstLast) {
      numbers.push(pages - 1);
      numbers.unshift(0);
    }
  }

  return numbers;
}
